.round {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.round .content {
  flex-grow: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 30px 1.5rem 6rem;
  padding: 30px 1.5rem 0rem;
}
.round .player-profile {
  display: flex;
  flex-direction: column;
  width: 24rem;
  min-height: 100%;
  align-items: center;
  padding: 3rem 4rem;
}
.round .player-profile .profile-avatar {
  width: 8rem;
  height: 8rem;
  margin: 3rem 0;
}
.round .player-profile .profile-score {
  text-align: center;
}
.round .player-profile .profile-score span {
  font-size: 3rem;
  font-weight: bold;
}
.round .player-profile .timer {
  margin-top: 2rem;
  text-align: center;
}
.round .player-profile .timer .seconds {
  font-size: 3rem;
  font-family: Helvetica, Arial, sans-serif;
  color: green;
}
.round .player-profile .timer.lessThan10 .seconds {
  color: orange;
}
.round .player-profile .timer.lessThan5 .seconds {
  color: red;
}
.round .task {
  align-content: right;
  padding: 4rem 4rem 0 4rem;
}
.round .task .task-response {
  margin: 3rem 0 0;
}
.round .task .task-response form {
  width: 40rem;
}
.round .task .task-response form button[type="submit"] {
  margin-top: 2rem;
}
.round .task .task-response .response-submitted {
  background-color: #e8e8e8;
  padding: 2rem;
  border-radius: 0.4rem;
}
.round .task .task-response .response-submitted h5 {
  margin: 0 0 0.5rem;
  font-size: inherit;
}
.round .social-exposure {
  margin: 4rem;
}
.round .social-exposure .alter {
  display: flex;
  padding-right: 3rem;
}
.round .social-exposure .alter img {
  width: 6rem;
  height: 6rem;
  margin-right: 3rem;
}
.round .social-exposure .alter .range {
  display: flex;
  align-items: center;
  justify-content: center;
}
.round .metadata-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.round .player-avatar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid white;
  border-radius: 50px;
}
.round .player-name-container {
  font-family: "Palatino Linotype", "Book Antiqua", Palatino, serif;
  font-style: italic;
  text-transform: uppercase;
  font-weight: normal;
  color: var(--darkblue);
}
.round .round-number-container {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-size: 16px;
  color: var(--turquoise);
}
button {
  padding: 0.8rem 1.2rem;
  border-radius: 0.3rem;
  border: 1px solid lightgray;
  background-color: #eee;
}
button:hover {
  background-color: #ddd;
}
.instructions button,
.quiz button,
.exit-survey button {
  margin: 2rem 1rem 0 0;
}
.instructions label,
.quiz label,
.exit-survey label {
  display: block;
  margin: 2rem 0 1rem;
}
.instructions input[type="text"],
.quiz input[type="text"],
.exit-survey input[type="text"],
.instructions input[type="number"],
.quiz input[type="number"],
.exit-survey input[type="number"] {
  padding: 0.3rem 0.5rem;
  border-radius: 0.3rem;
  border: 1px solid lightgray;
}
.instructions input[type="radio"],
.quiz input[type="radio"],
.exit-survey input[type="radio"] {
  margin-right: 0.5rem;
}
.instructions textarea,
.quiz textarea,
.exit-survey textarea {
  padding: 0.3rem 0.5rem;
  border-radius: 0.3rem;
  border: 1px solid lightgray;
}
.exit-survey .form-line {
  display: flex;
}
.exit-survey .form-line > div {
  margin-right: 30px;
}
.exit-survey .form-line > div textarea {
  min-height: 90px;
  width: 100%;
}
.exit-survey .form-line.thirds > div {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 30%;
}
.exit-survey .form-line.thirds > div label {
  min-height: 36px;
}
.finished {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.game {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  margin: -40px -1.5rem -6rem;
  padding-top: 40px;
}
